<template>
    <div>
        <div class="loginNavFix">
            <img src="../../../assets/login/return.jpg" alt="" class="return" @click="returns">
            <p class="title f30 cfff text-center">教材资源</p>
            <p class="navBtn f24 cfff pl-30" v-if="idManage" @click="changeManage">取消</p>
            <p class="navBtn f24 cfff pl-30" v-else @click="changeManage">管理</p>
        </div>
    
        <div class="item-for">
            <div class="item-list pr-30" v-for="(item,index) in list" :key="index"  @click="linkTo(index)">
                <div class="cc3 home-lin2">
                    <div v-if="idManage">
                        <img class="checked" v-if="item.checked" src="../../../assets/index/checked.png" alt="">
                        <img class="noChecked" v-else src="../../../assets/index/noChecked.png" alt="">
                    </div>
                    <p class="item-con c000 f30">{{item.title}}</p>
                    <span class="item-time f24">{{item.create_time | fileTime(item.create_time)}}</span>
                </div>
            </div>
        </div>
        
        <div v-if="idManage" class="deleteBo">
            <div class="allBtn" @click="changeAllChecked">
                <img v-if="allChecked" class="allCheckedCanleNo" src="../../../assets/index/checked.png" alt="">
                <img v-else class="allCheckedCanle ml-28 mr-30" src="../../../assets/index/noChecked.png" alt="">
                <div class="allText f26 c000">全选</div>
            </div>
            <div class="deleteBtn f28 cfff" @click="deleteFn">删除</div>
        </div>
        <div v-else class="centerBtn f26 cfff text-center" @click="next">新增文章</div>

        <van-popup v-model="shows" position="right" class="vantPop">
            <div class="loginNavFix">
                <img src="../../../assets/login/return.jpg" alt="" class="return" @click="closePop">
                <p class="title f30 cfff text-center">教材资源</p>
                <p class="navBtn f24 cfff pl-30" @click="closePop">取消</p>
            </div>
        
            <div class="item-for">
                <div class="item-list pr-30" v-for="(item,index) in list" :key="index"  @click="linkTo(index)">
                    <div class="cc3 home-lin2">
                        <div>
                            <img class="checked" v-if="item.checked" src="../../../assets/index/checked.png" alt="">
                            <img class="noChecked" v-else src="../../../assets/index/noChecked.png" alt="">
                        </div>
                        <p class="item-con c000 f30">{{item.title}}</p>
                        <span class="item-time f24">{{item.create_time | fileTime(item.create_time)}}</span>
                    </div>
                </div>
            </div>
            
            <div class="deleteBo">
                <div class="allBtn" @click="changeAllChecked">
                    <img v-if="allChecked" class="allCheckedCanleNo" src="../../../assets/index/checked.png" alt="">
                    <img v-else class="allCheckedCanle ml-28 mr-30" src="../../../assets/index/noChecked.png" alt="">
                    <div class="allText f26 c000">全选</div>
                </div>
                <div class="deleteBtn f28 cfff" @click="deleteFn">删除</div>
            </div>

        </van-popup>

    </div>
</template>

<script>
import {publisFn} from '../../../lib/util'
export default {
  name: 'HelloWorld',
    data () {
        return {
            columns: ['全部科目', '化学', '英语', '语文', '数学', '美术'],
            columnClass: ['1111', '2222', '英语', '语文', '数学', '美术'],
            show:true,
            shows:false,
            subject:true,
            level:true,
            modelShow:false,
            modelShow2:false,
            nowSbject:'全部科目',
            nowClass:'11111',
            idManage:false,
            allChecked:false,
            list:[]
        }
    },
    filters : {
        fileTime(num){
           return publisFn.dateFormat(num);
        }
    },
    methods : {
        returns(){
            this.$router.push({path:'/indexs'});//返回上一级
        },
        linkTo(index){
            if(this.idManage){
                this.list[index].checked = !this.list[index].checked
            }else{
                this.$router.push({path:'/teachingresourcesdetail',query:{id:this.list[index].id}})
            }
        },
        changeManage(){
            this.shows = true;
            this.idManage = !this.idManage;
        },
        closePop(){
            this.shows = false;
            this.idManage = !this.idManage;
        },
        next(){//发布文章
            this.$router.push({path:'/setteachingresources'})
        },
        changeAllChecked(){
            var that = this;
            this.allChecked = !this.allChecked;
            if(this.allChecked){
                for(var i in that.list){
                    that.list[i].checked = true;
                }
            }else{
                for(var i in that.list){
                    that.list[i].checked = false;
                }
            }
        },
        deleteFn(){
            var that = this;
            var ids = [];
            for(var j in that.list){
                if(that.list[j].checked){
                    ids.push(that.list[j].id)
                }
            }
            alert(ids.toString())
        },
        getAllResourceByTeacherId(){
            this.$toast.loading({
                mask: true,
                duration:0
            });
            this.$http({url:this.$api.teachingresources.getAllResourceByTeacherId},{teacherId:JSON.parse(localStorage.getItem('zhxy_user')).id}).then(data => {
                console.log(data);
                if(data.code == 0){
                    for(var i in data.data){
                        data.data[i].checked = false;
                    }
                    this.list = data.data;
                    this.$toast.clear();
                }else{
                    this.$toast(data.msg);
                }
            },err => {
                this.$toast.clear();
            })
        }

    },
    created() {
        this.getAllResourceByTeacherId();
    },
}
</script>

<style scoped>
    
    /* .item-for{width: 100%;overflow: hidden;padding-bottom: 1rem;margin-top: 0.84rem;} */
    .item-for{width: 100%;overflow: auto;position: absolute;top: 0.84rem;left: 0;right: 0;bottom: 2rem;}
    .item-list{height: 1.6rem;margin-left: 0.3rem;border-bottom: 1px solid #f2f2f2;box-sizing: border-box;overflow: hidden;}
    .item-title{float: left;}
    .item-img{float: right;margin-top: 0.12rem;width: 0.14rem;height: 0.14rem;display: block;}
    .home-lin1{clear: both;overflow: hidden;}
    .home-lin2{clear: both;overflow: hidden;}
    .isChecked{width: 0.4rem;height: 0.4rem;border-radius: 50%;float: left;box-sizing: border-box;margin: 0.6rem 0.28rem 0 0}
    .checked{width: 0.4rem;height: 0.4rem;margin: 0.6rem 0.28rem 0 0;float: left;}
    .noChecked{width: 0.6rem;height: 0.6rem;margin: 0.5rem 0.18rem 0 -0.1rem;float: left;}
    .item-con{float: left;white-space: nowrap;overflow: hidden;text-overflow: ellipsis;width: 4rem;line-height: 1.6rem;}
    .item-time{float: right;line-height: 1.6rem;}
    .centerBtn{height: 0.9rem;width: 5rem;border-radius: 0.9rem; background: linear-gradient(to bottom right, #82cbff , #7b7ceb);line-height: 0.9rem; box-shadow: 0 0 10px #f0f9ff; position: fixed;bottom: 0.63rem;left: 50%;margin-left: -2.5rem; }
    .deleteBo{position: fixed;bottom: 0;left: 0;width: 100%;height: 0.9rem;background: #f3f3f3;}
    .allCheckedCanle{width: 0.6rem;height: 0.6rem;margin-top: 0;float: left;margin-top: 0.15rem;}
    .allCheckedCanleNo{width: 0.4rem;height: 0.4rem;margin-top: 0;float: left;margin:0.25rem 0.4rem 0 0.38rem;}
    .allBtn{float: left;}
    .allText{float: left;line-height: 0.9rem;}
    .deleteBtn{float: right;width: 2rem;height: 0.9rem;background: #e74444;text-align: center;line-height: 0.9rem;}
    .vantPop{width: 100vw;height: 100vh;z-index: 999999 !important;}
</style>





